import { Outlet } from "react-router-dom";

const Layout = () => {
  return (
    <div
      style={{
        height: "100vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "#f4f4f4",
      }}
    >
      <Outlet />
    </div>
  );
};

export default Layout;
