import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  Card,
  CardHeader,
  Button,
  Row,
  Col,
  Label,
  InputGroup,
  InputGroupText,
  Input,
  Badge,
  Spinner,
} from "reactstrap";
import { Icon } from "@iconify/react";
import DataTable from "react-data-table-component";
import DataTablePagination from "../components/DataTablePagination";
import useAuth from '../hooks/useAuth'
import Http from "../services/Http";
import { checkMembership } from "../services/Utils";
import { toast } from 'react-toastify'
import "./ProjectList.css";

const ProjectList = () => {
  const { user } = useAuth();
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const rowsPerPageOptions = [25, 50, 100, 200];
  const [totalRows, setTotalRows] = useState(1000);
  const [loading, setLoading] = useState(false);
  const [projects, setProjects] = useState([]);
  const [isGetData, setIsGetData] = useState(false);
  const columns = [
    {
      name: "Name",
      sortable: true,
      selector: (row) => (
        <Link to={`/projects/${row.id}`} className="link">
          {row.name}
        </Link>
      ),
    },
    {
      name: "Check status",
      sortable: true,
      width: "130px",
      selector: (row) => renderStatusBadge(row),
    },
    {
      name: "Nb links",
      sortable: true,
      width: "60px",
      selector: (row) =>
        row.all_total_urls.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " "),
    },
    {
      name: "Indexed %",
      sortable: true,
      width: "102px",
      selector: (row) => renderIndexedBadge(row),
    },
    {
      name: <div>Indexing in progress</div>,
      sortable: true,
      width: "108px",
      hide: "sm",
      selector: (row) => renderIndexingProgressBadge(row),
    },
    {
      name: <div>Non indexed (redirected)</div>,
      sortable: true,
      width: "118px",
      hide: "md",
      selector: (row) => renderNonIndexedBadge(row),
    },
    {
      name: <div>Non indexables (redirected)</div>,
      sortable: true,
      width: "136px",
      hide: "md",
      selector: (row) => renderNonIndexableBadge(row),
    },
    {
      name: "Creation date",
      sortable: true,
      width: "150px",
      hide: "lg",
      selector: (row) => row.created_at,
    },
    {
      name: "Updated date",
      sortable: true,
      width: "152px",
      hide: "lg",
      selector: (row) => row.updated_at,
    },
  ];

  useEffect(() => {
    fetchProjects();
  }, [search, rowsPerPage, currentPage, isGetData]);

  const fetchProjects = async () => {
    if (checkMembership(user)) {
      setLoading(true);
      const { data } = await Http.get("/projects", {
        params: {
          search: search,
          page: currentPage,
          rowsPerPage: rowsPerPage,
        },
      });
  
      setProjects(data.data);
      setTotalRows(data.recordsFiltered);
      setLoading(false);
    } else {
      toast.error('Subscribe to our membership to take advantage of the features provided please.')
    }
  }

  const renderStatusBadge = (row) => {
    const status =
      row.completed_urls === 0 && row.total_urls > 0
        ? 0
        : row.completed_urls === row.total_urls
        ? 2
        : 1;
    const progress =
      row.total_urls > 0
        ? ((row.completed_urls / row.total_urls) * 100).toFixed(0)
        : 0;

    switch (status) {
      case 0:
        return (
          <Badge className="badge-dim" color="warning">
            In progress
          </Badge>
        );
      case 1:
        return (
          <Badge className="badge-dim" color="warning">
            In progress ({progress}%)
          </Badge>
        );
      case 2:
        return (
          <Badge className="badge-dim" color="success">
            Checked
          </Badge>
        );
      case 3:
        return (
          <Badge className="badge-dim" color="primary">
            Canceled
          </Badge>
        );
      default:
        return (
          <Badge className="badge-dim" color="warning">
            In progress
          </Badge>
        );
    }
  };

  const renderIndexedBadge = (row) => {
    const indexedPercent =
      row.total_urls > 0
        ? ((row.indexed_urls / row.total_urls) * 100).toFixed(0)
        : 0;
    return (
      <span className="d-flex align-items-center text-primary">
        <Icon icon="radix-icons:dot-filled" fontSize={20} />
        <span>
          {row.indexed_urls} ({indexedPercent}%)
        </span>
      </span>
    );
  };

  const renderIndexingProgressBadge = (row) => {
    const refundedPercent =
      row.total_urls > 0
        ? ((row.non_indexed_urls / row.total_urls) * 100).toFixed(0)
        : 0;
    return (
      <span className="d-flex align-items-center text-warning">
        <Icon icon="radix-icons:dot-filled" fontSize={20} />
        <span>
          {row.non_indexed_urls} ({refundedPercent}%)
        </span>
      </span>
    );
  };

  const renderNonIndexedBadge = (row) => {
    const refundedPercent =
      row.total_urls > 0
        ? ((row.refunded_urls / row.total_urls) * 100).toFixed(0)
        : 0;
    return (
      <span className="d-flex align-items-center text-danger">
        <Icon icon="radix-icons:dot-filled" fontSize={20} />
        <span>
          {row.refunded_urls} ({refundedPercent}%)
        </span>
      </span>
    );
  };

  const renderNonIndexableBadge = (row) => {
    return (
      <span className="d-flex align-items-center text-danger">
        <Icon icon="radix-icons:dot-filled" fontSize={20} />
        <span>{row.nonindexable_refunded_urls}</span>
      </span>
    );
  };

  const handleReloadData = () => {
    setIsGetData((isGetData) => !isGetData);
  };

  return (
    <Card>
      <CardHeader className="p-3 bg-white d-flex flex-wrap align-items-center justify-content-between">
        <h4 className="mb-1">My Projects</h4>
        <div className="mb-1">
          <Button color="primary" size='sm' className="me-2" onClick={handleReloadData}>
            <Icon icon="mdi:reload" fontSize={20} /> <span>Reload Data</span>
          </Button>
          <Link className="btn btn-success btn-sm" to="/projects/new">
            <Icon icon="mdi:web-plus" fontSize={20} /> <span>New Project</span>
          </Link>
        </div>
      </CardHeader>
      <div className="dataTables_wrapper dt-bootstrap4 no-footer">
        <Row className="border-bottom justify-between p-3 g-2">
          <Col className="col-7 text-start" lg="3" sm="4">
            <div className="dataTables_filter">
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ri:search-line" />
                </InputGroupText>
                <Input
                  type="search"
                  placeholder="Search..."
                  value={search}
                  onChange={(ev) => setSearch(ev.target.value)}
                />
              </InputGroup>
            </div>
          </Col>
          <Col className="col-5 text-end" lg="9" sm="8">
            <div className="datatable-filter">
              <div className="dataTables_length">
                <Label className="mb-0">
                  <Input
                    type="select"
                    value={rowsPerPage}
                    onChange={(ev) => setRowsPerPage(ev.target.value)}
                  >
                    {rowsPerPageOptions.map((rowsPerPageOption, idx) => (
                      <option key={idx} value={rowsPerPageOption}>
                        {rowsPerPageOption}
                      </option>
                    ))}
                  </Input>
                </Label>
              </div>
            </div>
          </Col>
        </Row>
        <DataTable
          className="border-bottom"
          columns={columns}
          data={projects}
          pagination
          paginationServer
          paginationTotalRows={totalRows}
          paginationPerPage={rowsPerPage}
          paginationRowsPerPageOptions={rowsPerPageOptions}
          paginationComponent={({ rowCount, onChangeRowsPerPage }) => (
            <DataTablePagination
              currentPage={currentPage}
              rowsPerPage={rowsPerPage}
              rowCount={rowCount}
              onChangePage={setCurrentPage}
            />
          )}
          progressPending={loading}
          progressComponent={
            <div
              className="d-flex justify-content-center align-items-center"
              style={{ minHeight: 100 }}
            >
              <Spinner size="sm" color="dark" className="me-1" />{" "}
              <span>Please wait for a moment...</span>
            </div>
          }
          sortIcon={
            <div>
              <span>&darr;</span>
              <span>&uarr;</span>
            </div>
          }
          customStyles={{
            headCells: {
              style: {
                paddingLeft: "12px !important",
                paddingRight: "5px !important",
              },
            },
            cells: {
              style: {
                paddingLeft: "12px !important",
                paddingRight: "5px !important",
              },
            },
          }}
        />
      </div>
    </Card>
  );
};

export default ProjectList;
