import { Pagination, PaginationItem, PaginationLink } from "reactstrap";
import { Icon } from "@iconify/react";

const DataTablePagination = ({
  currentPage,
  rowsPerPage,
  rowCount,
  onChangePage,
}) => {
  const pageNumbers = [];
  for (let i = 1; i <= Math.ceil(rowCount / rowsPerPage); i++) {
    pageNumbers.push(i);
  }

  const paginationNumber = () => {
    if (pageNumbers.length <= 5) {
      return pageNumbers;
    } else if (pageNumbers.length >= 5 && currentPage <= 4) {
      return [1, 2, 3, 4, 5, "...", pageNumbers[pageNumbers.length - 1]];
    } else if (
      pageNumbers.length >= 5 &&
      currentPage >= pageNumbers[pageNumbers.length - 4]
    ) {
      return [
        1,
        "...",
        pageNumbers[pageNumbers.length - 5],
        pageNumbers[pageNumbers.length - 4],
        pageNumbers[pageNumbers.length - 3],
        pageNumbers[pageNumbers.length - 2],
        pageNumbers[pageNumbers.length - 1],
      ];
    } else if (
      pageNumbers.length > 5 &&
      currentPage > 4 &&
      currentPage < pageNumbers[pageNumbers.length - 4]
    ) {
      return [
        1,
        "...",
        currentPage - 1,
        currentPage,
        currentPage + 1,
        "...",
        pageNumbers[pageNumbers.length - 1],
      ];
    }
  };
  let paginationItms = paginationNumber();
  return (
    <div className="d-flex align-items-center justify-content-center my-3">
      <Pagination>
        <PaginationItem disabled={currentPage - 1 === 0 ? true : false}>
          <PaginationLink
            className="page-link-first"
            href="#first"
            onClick={(ev) => {
              ev.preventDefault();
              onChangePage(1);
            }}
          >
            <Icon icon="icon-park-outline:double-left" />
          </PaginationLink>
        </PaginationItem>
        <PaginationItem disabled={currentPage - 1 === 0 ? true : false}>
          <PaginationLink
            className="page-link-prev"
            href="#prev"
            onClick={(ev) => {
              ev.preventDefault();
              onChangePage(currentPage - 1);
            }}
          >
            <Icon icon="icon-park-outline:left" />
          </PaginationLink>
        </PaginationItem>
        {paginationItms.map((item, idx) => {
          return (
            <PaginationItem
              disabled={isNaN(item)}
              className={`d-none d-sm-block ${
                currentPage === item ? "active" : ""
              }`}
              key={idx}
            >
              <PaginationLink
                tag="a"
                href="#pageitem"
                onClick={(ev) => {
                  ev.preventDefault();
                  onChangePage(item);
                }}
              >
                {item}
              </PaginationLink>
            </PaginationItem>
          );
        })}
        <PaginationItem
          disabled={
            pageNumbers[pageNumbers.length - 1] === currentPage ? true : false
          }
        >
          <PaginationLink
            className="page-link-first"
            href="#next"
            onClick={(ev) => {
              ev.preventDefault();
              onChangePage(currentPage + 1);
            }}
          >
            <Icon icon="icon-park-outline:right" />
          </PaginationLink>
        </PaginationItem>
        <PaginationItem
          disabled={
            pageNumbers[pageNumbers.length - 1] === currentPage ? true : false
          }
        >
          <PaginationLink
            className="page-link-prev"
            href="#last"
            onClick={(ev) => {
              ev.preventDefault();
              onChangePage(pageNumbers[pageNumbers.length - 1]);
            }}
          >
            <Icon icon="icon-park-outline:double-right" />
          </PaginationLink>
        </PaginationItem>
      </Pagination>
    </div>
  );
};

export default DataTablePagination;
