import { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Row,
  Col,
  FormGroup,
  Label,
  Input,
  Alert,
  Spinner,
  Button,
} from "reactstrap";
import { Icon } from "@iconify/react";
import Http from "../services/Http";
import useAuth from '../hooks/useAuth'
import { checkMembership } from "../services/Utils";
import { toast } from "react-toastify";

const NewLinks = () => {
  const params = useParams();
  const { user } = useAuth()
  const [loading, setLoading] = useState(false);
  const [links, setLinks] = useState("");
  const [linkLimit, setLinkLimit] = useState(5);

  useEffect(() => {
    fetchLimitLinks();
  }, []);

  const fetchLimitLinks = async () => {
    const { data } = await Http.get("/projects/limit-links");
    setLinkLimit(data);
  };

  const handleChangeLinkList = (ev) => {
    let length = ev.target.value.split(/\r*\n/).length;
    if (length > linkLimit) {
      toast.error(`You can add maximum ${linkLimit} links at once.`);
    } else {
      setLinks(ev.target.value);
    }
  };

  const handleCreateLinks = async (ev) => {
    if (checkMembership(user)) {
      if (links.trim() === "") {
        toast.error("Please add a link.");
      } else if (links.split(/\r*\n/).length > linkLimit) {
        toast.error(`You can add maximum ${linkLimit} links at once.`);
      } else {
        try {
          setLoading(true);
          const { data } = await Http.post(`/projects/${params.id}/links`, {
            user: user._id,
            urlslist: links,
          });
  
          if (data.status) {
            setLinks("");
            for (let msg of data.msgs) {
              toast.success(msg);
            }
          } else {
            for (let msg of data.msg) {
              toast.error(msg);
            }
          }
        } catch (err) {
          toast.error(err.message);
        } finally {
          setTimeout(() => setLoading(false), 500);
        }
      }
    } else {
      toast.error('Subscribe to our membership to take advantage of the features provided please.')
    }
  };

  return (
    <Card>
      <CardHeader className="bg-white p-3 d-flex flex-wrap align-items-center justify-content-between ">
        <h4 className="mb-0">Add new links</h4>
        <Link
          className="btn btn-primary btn-sm d-flex align-items-center"
          to={`/projects/${params.id}`}
        >
          <Icon icon="mingcute:back-2-fill" fontSize={20} className="me-2" />
          <span>Return to project</span>
        </Link>
      </CardHeader>
      <CardBody>
        <FormGroup className="required">
          <Row>
            <Col xl={3} lg={3} md={4} sm={5} xs={12}>
              <Label>Links list (one by line)</Label>
            </Col>
            <Col xl={9} lg={8} md={8} sm={7} xs={12}>
              <Input
                type="textarea"
                value={links}
                onChange={handleChangeLinkList}
                rows={10}
              />
            </Col>
          </Row>
        </FormGroup>
        <FormGroup>
          <Row>
            <Col xl={3} lg={4} md={4} sm={5} xs={12}>
              <Label>Nb estimated credit(s) avec dduplication</Label>
            </Col>
            <Col xl={9} lg={8} md={8} sm={7} xs={12}>
              <p className="mb-1">
                {links.split(/\r*\n/).filter((link) => link !== "").length}
              </p>
            </Col>
          </Row>
        </FormGroup>
        <Alert className="alert-icon" color="warning">
          <Icon icon="carbon:warning" fontSize={24} className="me-2" />
          <strong className="me-1">Warning!</strong> On project saving, credits
          will be deducted according to the number of urls. Urls not indexed
          after 10 days will be re-credited.
        </Alert>
      </CardBody>
      <CardFooter className="bg-white d-flex justify-content-end py-3">
        <Button
          color="primary"
          disabled={loading}
          className="d-flex align-items-center"
          onClick={handleCreateLinks}
        >
          {loading ? (
            <Spinner size="sm" color="light" className="me-2" />
          ) : (
            <Icon
              icon="material-symbols:save-outline"
              fontSize={20}
              className="me-2"
            />
          )}
          <span>Create Links</span>
        </Button>
      </CardFooter>
    </Card>
  );
};

export default NewLinks;
