import { Fragment, useState } from "react";
import { Badge, Tooltip } from "reactstrap";

const IndexableBadge = ({ row }) => {
  const [tooltipOpen, setTooltipOpen] = useState(false);
  if (parseInt(row.indexable) === 1) {
    let val_msg = row.error_reason === "legacy" ? "Lagacy" : row.http_code;
    let msgIndexable =
      row.http_code >= 200 && row.http_code < 300
        ? "Indexable page"
        : `Sent for indexing despite http status : ${row.http_code}`;
    if (row.error_reason === "legacy") {
      msgIndexable += ": activation after 04/08/2022";
    }
    let color =
      row.http_code >= 200 && row.http_code < 300 ? "success" : "warning";
    let label =
      row.http_code >= 200 && row.http_code < 300 ? "YES" : "NO but sent";

    return (
      <Fragment>
        <Badge
          id={`indexable-badge-${row.id}`}
          className="badge-dim px-2 py-1"
          color={color}
        >
          {label}({val_msg})
        </Badge>
        <Tooltip
          placement="bottom"
          isOpen={tooltipOpen}
          target={`indexable-badge-${row.id}`}
          toggle={() => setTooltipOpen((tooltipOpen) => !tooltipOpen)}
        >
          {msgIndexable}
        </Tooltip>
      </Fragment>
    );
  } else if (parseInt(row.indexable) === 2) {
    let errorReason = row.http_code;
    if (row.error_reason === "invalid_domain") {
      errorReason = "inaccessible";
    } else if (row.error_reason === "canonical") {
      errorReason = "canonical";
    } else if (row.error_reason === "noindex") {
      errorReason = "Meta noindex";
    } else if (row.error_reason === "noindex_header") {
      errorReason = "X-Robots-Tag: noindex";
    }

    return (
      <Fragment>
        <Badge
          id={`indexable-badge-${row.id}`}
          className="badge-dim px-2 py-1"
          color="danger"
        >
          NO ({errorReason})
        </Badge>
        <Tooltip
          placement="bottom"
          isOpen={tooltipOpen}
          target={`indexable-badge-${row.id}`}
          toggle={() => setTooltipOpen((tooltipOpen) => !tooltipOpen)}
        >
          {errorReason}
        </Tooltip>
      </Fragment>
    );
  }

  return (
    <Fragment>
      <Badge className="badge-dim px-2 py-1" color="warning">
        Waiting
      </Badge>
    </Fragment>
  );
};

export default IndexableBadge;
