import { useState } from 'react'
import Sidebar from "./Sidebar";
import Header from "./Header";
import Drawer from "./Drawer";
import { Outlet } from "react-router-dom";

import "./Index.css";

const Layout = () => {
  const [expand, setExpand] = useState(false);

  return (
    <div className="imn-layout">
      <Sidebar />
      <div className="imn-layout-content">
        <Header onOpen={() => setExpand(true)}/>
        <div className="imn-content">
          <Outlet />
        </div>
      </div>
      <Drawer open={expand} onClose={() => setExpand(false)}/>
    </div>
  );
};

export default Layout;
