import { useState, useEffect } from "react";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { Icon } from "@iconify/react";
import useAuth from "../../hooks/useAuth";
import Http from "../../services/Http";
import { toast } from "react-toastify";
import { checkMembership } from "../../services/Utils";

const ExportDropdown = ({ id }) => {
  const { user, setLoading } = useAuth();
  const [isOpen, setIsOpen] = useState(false);

  const handleExportCsvUrls = async (type) => {
    if (checkMembership(user)) {
      try {
        setLoading(true);
        const { data } = await Http.post(`/projects/${id}/export/csv/${type}`);
        if (data.status) {
          let downloadLink = document.createElement("a");
          let fileData = ["\ufeff" + data.urls];
          let blobObject = new Blob(fileData, {
            type: "text/csv;charset=utf-8;",
          });
          let url = URL.createObjectURL(blobObject);
          downloadLink.href = url;
          downloadLink.download = `project_links_${type}_${id}.csv`;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        } else {
          for (let msg of data.msgs) {
            toast.error(msg);
          }
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setLoading(false);
      }
    } else {
      toast.error('Subscribe to our membership to take advantage of the features provided please.')
    }
  };

  const handleExportTxtUrls = async (type) => {
    if (checkMembership(user)) {
      try {
        setLoading(true);
        const { data } = await Http.post(`/projects/${id}/export/txt/${type}`);
        if (data.status) {
          let downloadLink = document.createElement("a");
          let fileData = ["\ufeff" + data.urls];
          let blobObject = new Blob(fileData, {
            type: "plain/text;charset=utf-8;",
          });
          let url = URL.createObjectURL(blobObject);
          downloadLink.href = url;
          downloadLink.download = `project_links_${type}_${id}.txt`;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        } else {
          for (let msg of data.msgs) {
            toast.error(msg);
          }
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setLoading(false);
      }
    } else {
      toast.error('Subscribe to our membership to take advantage of the features provided please.')
    }
  };

  return (
    <Dropdown
      className="me-2 mb-1"
      isOpen={isOpen}
      toggle={() => setIsOpen((open) => !open)}
    >
      <DropdownToggle color="primary" className="btn-sm d-flex align-items-center">
        <Icon
          icon="material-symbols:export-notes-outline-rounded"
          fontSize={20}
          className="me-2"
        />
        <span>Export Urls</span>
        <Icon icon="mdi:chevron-down" fontSize={18} className="ms-2" />
      </DropdownToggle>
      <DropdownMenu className="mt-2">
        <DropdownItem onClick={() => handleExportCsvUrls("all")}>
          <Icon name="file-text" />
          <span>CSV all columns links</span>
        </DropdownItem>
        <DropdownItem onClick={() => handleExportTxtUrls("all")}>
          <Icon name="file-text" />
          <span>TXT all links</span>
        </DropdownItem>
        <DropdownItem onClick={() => handleExportTxtUrls("refunded")}>
          <Icon name="file-text" />
          <span className="text-danger">
            TXT all non indexed links
          </span>
        </DropdownItem>
      </DropdownMenu>
    </Dropdown>
  );
};

export default ExportDropdown;
