import {
  Navbar,
  Nav,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button,
} from "reactstrap";
import { Link, useNavigate } from "react-router-dom";
import { Icon } from "@iconify/react";
import Avatar from "../components/Avatar";
import useAuth from "../hooks/useAuth";

const Header = ({ onOpen }) => {
  const { user, setUser } = useAuth();
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.clear();
    setUser(null);
    navigate("/login");
  };

  return (
    <Navbar className="imn-header" container="fluid">
      <Nav className="me-auto" navbar>
        <Button color="link" size="sm" className="nav-toggle-btn px-2 text-dark" onClick={onOpen}>
          <Icon icon="mdi:menu" fontSize={25} />
        </Button>
      </Nav>
      <Nav className="ms-auto" navbar>
        <UncontrolledDropdown>
          <DropdownToggle nav className="py-0 px-2">
            <div className="d-flex">
              <Avatar image="/avatar.png" width={45} height={45} />
              <div className="d-flex flex-column ms-2">
                <p className="mb-0 text-secondary">User</p>
                <p className="mb-0 d-flex align-items-center">
                  <span>
                    {user?.firstName} {user?.lastName}
                  </span>
                  <Icon icon="iconamoon:arrow-down-2" />
                </p>
              </div>
            </div>
          </DropdownToggle>
          <DropdownMenu flip className="mt-2">
            <DropdownItem>
              <div className="d-flex align-items-center">
                <Avatar image="/avatar.png" width={40} height={40} />
                <div className="d-flex flex-column ms-2">
                  <strong>
                    {user?.firstName} {user?.lastName}
                  </strong>
                  <p className="mb-0">{user?.email}</p>
                </div>
              </div>
            </DropdownItem>
            <DropdownItem divider />
            <Link
              to={"/profile"}
              className="header-link"
            >
              <DropdownItem className="py-2">View Profile</DropdownItem>
            </Link>
            <DropdownItem divider />
            <DropdownItem className="py-2" onClick={handleLogout}>
              Logout
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      </Nav>
    </Navbar>
  );
};

export default Header;
