import { Spinner } from "reactstrap";

const LoadingScreen = () => {
  return (
    <div
      style={{
        zIndex: 100000,
        position: "fixed",
        top: 0,
        bottom: 0,
        width: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        background: "#00000099",
      }}
    >
      <Spinner color="light" className="me-2" />{" "}
      <span className="text-light fs-17px">Please wait for a moment...</span>
    </div>
  );
};

export default LoadingScreen;
