import { useMemo, useState } from "react";
import {
  Card,
  CardBody,
  Form,
  FormGroup,
  InputGroup,
  InputGroupText,
  Label,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { Icon } from "@iconify/react";
import Http from "../services/Http";
import { toast } from "react-toastify";
import "./ForgotPwd.css";

const ResetPwd = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { search } = useLocation();
  const [password, setPassword] = useState("");
  const [confirmPwd, setConfirmPwd] = useState("");

  const handleResetPwd = async () => {
    if (password.trim() === "") {
      toast.error("Please enter a password.");
    } else if (password.trim() !== confirmPwd.trim()) {
      toast.error("Passwords don't match.");
    } else {
      try {
        setLoading(true);
        const query = new URLSearchParams(search);
        const token = query.get("token");

        const { data } = await Http.post("/auth/reset-pwd", {
          password,
          token,
        });

        if (data.status) {
          toast.success(data.msg);
          navigate("/login");
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  return (
    <div className="forgot-password-container">
      <Card className="forgot-password-form">
        <CardBody className="p-4">
          <Form className="p-1">
            <div className="text-center mb-3">
              <img src="/logo.png" height={60} alt="logo" />
            </div>
            <h4>Reset password</h4>
            <FormGroup className="required">
              <Label>New Password</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="solar:key-broken" />
                </InputGroupText>
                <Input
                  type="password"
                  value={password}
                  onChange={(ev) => setPassword(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
            <FormGroup className="required">
              <Label>Password Confirmation</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="mdi:lock-check-outline" />
                </InputGroupText>
                <Input
                  type="password"
                  value={confirmPwd}
                  onChange={(ev) => setConfirmPwd(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
            <Button
              color="primary"
              disabled={loading}
              className="w-100 mt-4 mb-2 d-flex align-items-center gap-2 justify-content-center"
              onClick={handleResetPwd}
            >
              {loading ? (
                <Spinner size="sm" />
              ) : (
                <Icon icon="ic:baseline-published-with-changes" fontSize={20} />
              )}
              Change Password
            </Button>
            <div className="text-center">
              <Link to="/login" className="link">
                Return to login
              </Link>
            </div>
          </Form>
        </CardBody>
      </Card>
    </div>
  );
};

export default ResetPwd;
