import { Fragment, useState, useEffect } from "react";
import {
  Card,
  CardHeader,
  CardBody,
  Row,
  Col,
  InputGroup,
  InputGroupText,
  Input,
  Spinner,
} from "reactstrap";
import { useParams, Link } from "react-router-dom";
import { Icon } from "@iconify/react";
import { toast } from "react-toastify";
import Http from "../services/Http";
import ExportDropdown from "../components/project-detail/ExportDropdown.jsx";
import IndexableBadge from "../components/project-detail/IndexableBadge";
import IndexingStatusBadge from "../components/project-detail/IndexingStatusBadge";
import DataTable from "react-data-table-component";
import DataTablePagination from "../components/DataTablePagination";
import useAuth from "../hooks/useAuth";
import { checkMembership } from "../services/Utils.js";
import HTTP_STATUS_CODES from "../services/HTTP_STATUS_CODES";
import "./ProjectDetail.css";

const ProjectDetail = () => {
  const params = useParams();
  const { user } = useAuth();
  const [project, setProject] = useState(null);
  const [category, setCategory] = useState(0);
  const [indexable, setIndexable] = useState("");
  const [indexed, setIndexed] = useState("");
  const [refunded, setRefunded] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const [search, setSearch] = useState("");
  const [totalRows, setTotalRows] = useState(1000);
  const [loading, setLoading] = useState(false);
  const [links, setLinks] = useState([]);
  const rowsPerPageOptions = [25, 50, 100, 200];
  const columns = [
    {
      name: "Url",
      grow: 1,
      wrap: 1,
      sortable: true,
      selector: (row) => (
        <a href={row.url} rel="noreferrer" className="link" target="_blank">
          {row.url}
        </a>
      ),
    },
    {
      name: "Creation date",
      width: "140px",
      sortable: true,
      selector: (row) => row.submitted_date,
    },
    {
      name: "Indexable?",
      width: "120px",
      sortable: true,
      selector: (row) => <IndexableBadge row={row} />,
    },
    {
      name: "Indexing status",
      width: "150px",
      sortable: true,
      selector: (row) => <IndexingStatusBadge row={row} />,
    },
    {
      name: <div>Snippet title, description, proof, info</div>,
      wrap: 1,
      grow: 1.5,
      sortable: true,
      selector: (row) => {
        if (parseInt(row.indexed) === 1) {
          return formatIndexedInfo(row);
        } else if (
          parseInt(row.indexable) === 1 &&
          parseInt(row.http_code) === 401
        ) {
          return formatNonIndexableInfo(row);
        } else if (
          parseInt(row.indexable) === 1 &&
          parseInt(row.http_code) > 266 &&
          parseInt(row.http_code) <= 290
        ) {
          return formatNonIndexableInfo(row);
        } else if (parseInt(row.indexable) === 2) {
          return formatNonIndexableInfo(row);
        }
      },
    },
  ];

  useEffect(() => {
    fetchProject();
  }, []);

  useEffect(() => {
    fetchLinks();
  }, [search, rowsPerPage, currentPage, category]);

  const fetchProject = async () => {
    const { id } = params;
    const { data } = await Http.get(`/projects/${id}`);
    setProject(data.project_data);
  };

  const renderNonIndexableRefundedPercent = () => {
    if (project) {
      return (
        (parseInt(project.nonindexable_refunded_urls) /
          parseInt(project.total_urls)) *
        100
      ).toFixed(2);
    } else {
      return Number(0).toFixed(2);
    }
  };

  const renderStatus = () => {
    if (project) {
      const status =
        project.completed_urls === 0 && project.total_urls > 0
          ? 0
          : project.completed_urls === project.total_urls
          ? 2
          : 1;
      switch (status) {
        case 0:
        case 1:
          return (
            <div>
              <img src="/images/pending.svg" alt="Pending" /> In progress
            </div>
          );
        case 2:
          return (
            <div>
              <img src="/images/complete.svg" alt="Complete" /> Complete
            </div>
          );
        default:
          return (
            <div>
              <img src="/images/complete.svg" alt="Complete" /> Completed
            </div>
          );
      }
    } else {
      return (
        <div>
          <img src="/images/complete.svg" alt="Complete" /> Completed
        </div>
      );
    }
  };

  const handleChangeCategory = (category) => {
    if (category === 0) {
      setIndexable("");
      setIndexed("");
      setRefunded("");
    } else if (category === 1) {
      setIndexable("");
      setIndexed(0);
      setRefunded(0);
    } else if (category === 2) {
      setIndexable("");
      setIndexed(1);
      setRefunded("");
    } else if (category === 3) {
      setIndexable(1);
      setIndexed("");
      setRefunded(1);
    } else if (category === 4) {
      setIndexable(2);
      setIndexed("");
      setRefunded(1);
    }
    setCategory(category);
  };

  const fetchLinks = async () => {
    if (checkMembership(user)) {
      setLoading(true);
      const { id } = params;
      const { data } = await Http.get(`/projects/${id}/links`, {
        params: {
          indexed,
          indexable,
          refunded,
          search,
          page: currentPage,
          rowsPerPage: rowsPerPage,
        },
      });

      setLinks(data.data);
      setTotalRows(data.recordsFiltered);
      setLoading(false);
    } else {
      toast.error(
        "Subscribe to our membership to take advantage of the features provided please."
      );
    }
  };

  const toTimestamp = (strDate) => {
    let datum = Date.parse(strDate);
    return datum / 1000;
  };

  const getTimestampPurgeCache = () => {
    let cacheDayDelayDeletion = 15;
    let currentDate = new Date();
    return Math.round(
      currentDate.getTime() / 1000 - cacheDayDelayDeletion * 24 * 60 * 60
    );
  };

  const formatIndexedInfo = (row) => {
    return (
      <div className="my-1">
        {row.title && <p className="my-1 text-primary">{row.title}</p>}
        {row.description && <p className="mb-1">{row.description}</p>}
        {toTimestamp(row.checked_at) > getTimestampPurgeCache() && (
          <Link
            to={`/projects/${row.project_id}/cache/${row.id}`}
            target="_blank"
          >
            <Icon icon="mdi:usb-flash-drive" fontSize={15} className="me-1" />
            <span>Proof on {row.checked_at} (available 30 days)</span>
          </Link>
        )}
      </div>
    );
  };

  const formatNonIndexableInfo = (row) => {
    let problem = null,
      solution = null,
      information = null;

    if (row.error_reason === "noindex") {
      problem = (
        <p className="my-1">
          <strong>Problem: </strong>
          This URL cannot be indexed because it has a meta tag in noindex :
          <span className="text-danger">
            &lt;meta name='robots' content='noindex'&gt;
          </span>
        </p>
      );
      solution = (
        <p className="my-1">
          <strong>Solution: </strong>
          You need to pass the value to{" "}
          <span className="text-success">
            &lt;meta name='robots' content='index'&gt;
          </span>{" "}
          and add it back to your project so this URL can be indexed.
        </p>
      );
    } else if (row.error_reason === "noindex_header") {
      problem = (
        <p className="my-1">
          <strong>Problem: </strong>
          This URL cannot be indexed because it has the argument :{" "}
          <span className="text-danger">X-Robots-Tag: noindex</span> in the HTTP
          header.
        </p>
      );
      solution = (
        <p className="my-1">
          <strong>Solution: </strong>
          You need to remove this argument or replace it with{" "}
          <span className="text-success">X-Robots-Tag: index</span> for this URL
          and add it back into your project so that this URL can be indexed.
        </p>
      );
    } else if (row.error_reason === "robots_txt") {
      problem = (
        <p className="my-1">
          <strong>Problem: </strong>
          This URL is{" "}
          <span className="text-success">blocked by the robots.txt</span>. It
          cannot be indexed.
        </p>
      );
      solution = (
        <p className="my-1">
          <strong>Solution: </strong>
          Check your <span className="text-success">robots.txt</span> and remove
          the indexing block for{" "}
          <span className="text-success">the Google search engine</span>. After
          that you can <strong>add it back to your project</strong> so that this
          URL can be indexed.
        </p>
      );
    } else if (row.redirect_url !== null) {
      if (row.error_reason === "canonical") {
        problem = (
          <p className="my-1">
            <strong>Problem: </strong>
            This URL{" "}
            <a
              href={row.url}
              rel="noreferrer"
              className="link-danger"
              target="_blank"
            >
              {row.url}
            </a>{" "}
            cannot be indexed because it has a{" "}
            <strong>link rel=canonical</strong> with a different url :{" "}
            <a
              href={row.redirect_url}
              rel="noreferrer"
              className="link-success"
              target="_blank"
            >
              {row.redirect_url}
            </a>
          </p>
        );
        solution = (
          <p className="my-1">
            <strong>Solution: </strong>
            Please add the URL{" "}
            <a
              href={row.redirect_url}
              rel="noreferrer"
              className="link-success"
              target="_blank"
            >
              {row.redirect_url}
            </a>{" "}
            that is in the canonical tag in your project so that this URL can be
            indexed.
          </p>
        );
      } else {
        problem = (
          <p className="my-1">
            <strong>Problem: </strong>
            This URL{" "}
            <a
              href={row.url}
              rel="noreferrer"
              className="link-danger"
              target="_blank"
            >
              {row.url}
            </a>{" "}
            redirects with status{" "}
            <span className="text-danger">{row.http_code}</span> to :{" "}
            <a
              href={row.redirect_url}
              rel="noreferrer"
              className="link-success"
              target="_blank"
            >
              {row.redirect_url}
            </a>
          </p>
        );
        solution = (
          <p className="my-1">
            <strong>Solution: </strong>
            Please add the redirect URL{" "}
            <a
              href={row.redirect_url}
              rel="noreferrer"
              className="link-success"
              target="_blank"
            >
              {row.redirect_url}
            </a>{" "}
            in your project so that this URL can be indexed.
          </p>
        );
      }
    } else if (row.error_reason === "http_code") {
      if (parseInt(row.http_code) >= 400 && parseInt(row.http_code) <= 499) {
        if (parseInt(row.http_code) === 404) {
          problem = (
            <p className="my-1">
              <strong>Problem: </strong>
              This URL can't be indexed because it has the HTTP status :{" "}
              <span className="text-danger">
                {getHttpCodeLabel(row.http_code)}
              </span>
            </p>
          );
          solution = (
            <p className="my-1">
              <strong>Solution: </strong>
              You must check that this URL returns the HTTP status{" "}
              <span className="text-success">200</span> and add it back to your
              project so that this URL can be indexed.
            </p>
          );
        } else {
          problem = (
            <p className="my-1">
              <strong>Problem: </strong>
              This URL can't be indexed because it has the HTTP status :{" "}
              <span className="text-danger">
                {getHttpCodeLabel(row.http_code)}
              </span>
            </p>
          );
          solution = (
            <p className="my-1">
              <strong>Solution: </strong>
              You must check that this URL returns the HTTP status{" "}
              <span className="text-success">200</span> and{" "}
              <span>add it back to your project</span> so that this URL can be
              indexed. If you have not secured this page, it is usually a{" "}
              <span>bad firewall configuration</span> or{" "}
              <span>
                your host is preventing access because of a security rule
              </span>
              . Be careful because Google can also be blocked after a crawl of
              several URLs.
            </p>
          );
        }
      } else if (
        parseInt(row.http_code) >= 500 &&
        parseInt(row.http_code) <= 599
      ) {
        problem = (
          <p className="my-1">
            <strong>Problem: </strong>
            This URL can't be indexed because it has the HTTP status :{" "}
            <span className="text-danger">
              {getHttpCodeLabel(row.http_code)}
            </span>
          </p>
        );
        solution = (
          <p className="my-1">
            <strong>Solution: </strong>
            You need to check that this URL returns HTTP status{" "}
            <span className="text-success">(200)</span> and{" "}
            <strong>add it back to your project</strong> so that this URL can be
            indexed. If your server was overloaded you can try again to{" "}
            <strong>add it to your project</strong>.
          </p>
        );
      } else if (parseInt(row.http_code) === 0) {
        problem = (
          <p className="my-1">
            <strong>Problem: </strong>This URL does not appear to be a valid URL
            because no server is responding.
          </p>
        );
        solution = (
          <p className="my-1">
            <strong>Solution: </strong>
            Please check that your domain has not expired or that the server is
            available and functional. Once this{" "}
            <strong>URL is accessible (200)</strong>, you can{" "}
            <strong>add it back to your project</strong> so that this URL can be
            indexed.
          </p>
        );
      } else {
        problem = (
          <p className="my-1">
            <strong>Problem: </strong>
            This URL can't be indexed because it has the HTTP status :{" "}
            <span className="text-danger">
              {getHttpCodeLabel(row.http_code)}
            </span>
          </p>
        );
        solution = (
          <p className="my-1">
            <strong>Solution: </strong>
            You must check that this URL returns the HTTP status{" "}
            <span className="text-success">200</span> and{" "}
            <span>add it back to your project</span> so that this URL can be
            indexed. If you have not secured this page, it is usually a{" "}
            <span>bad firewall configuration</span> or{" "}
            <span>
              your host is preventing access because of a security rule
            </span>
            . Be careful because Google can also be blocked after a crawl of
            several URLs.
          </p>
        );
      }
    } else if (row.error_reason === "invalid_domain") {
      problem = (
        <p className="my-1">
          <strong>Problem: </strong>This URL does not appear to be a valid URL
          because no server is responding.
        </p>
      );
      solution = (
        <p className="my-1">
          <strong>Solution: </strong>
          Please check that your domain has not expired or that the server is
          available and functional. Once this{" "}
          <strong>URL is accessible (200)</strong>, you can{" "}
          <strong>add it back to your project</strong> so that this URL can be
          indexed.
        </p>
      );
    } else if (
      parseInt(row.indexable) === 1 &&
      parseInt(row.http_code) === 401
    ) {
      problem = (
        <p className="my-1">
          <strong>Problem: </strong>
          This URL can't be indexed because it has the HTTP status :{" "}
          <span className="text-danger">{getHttpCodeLabel(row.http_code)}</span>
          <br />
          <br />
          However it was sent for indexing.
        </p>
      );
      solution = (
        <p className="my-1">
          <strong>Solution: </strong>
          You must check that this URL returns the HTTP status{" "}
          <span className="text-success">200</span> and
          <span>add it back to your project</span> so that this URL can be
          indexed. If you have not secured this page, it is usually a{" "}
          <span>bad firewall configuration</span> or
          <span>your host is preventing access because of a security rule</span>
          . Be careful because Google can also be blocked after a crawl of
          several URLs
        </p>
      );
    } else if (
      parseInt(row.indexable) === 1 &&
      parseInt(row.http_code) === 266
    ) {
      information = <p>Url protected by cloudflare</p>;
    } else if (
      parseInt(row.indexable) === 1 &&
      parseInt(row.http_code) === 267
    ) {
      information = <p>Url protected by AmazonS3</p>;
    }

    return (
      <div className="py-1">
        {problem && <div>{problem}</div>}
        {solution && <div>{solution}</div>}
        {information && <div>{information}</div>}
      </div>
    );
  };

  const getHttpCodeLabel = (code) => {
    var codeLabel = "CODE_".concat(code);
    return HTTP_STATUS_CODES[codeLabel]
      ? code + " (" + HTTP_STATUS_CODES[codeLabel] + ")"
      : code;
  };

  return (
    <Fragment>
      <Card className="mb-3">
        <CardHeader className="bg-white p-3">
          <div className="d-flex flex-wrap align-items-center justify-content-between">
            <h4>{project?.name}</h4>
            <div className="d-flex flex-wrap">
              <Link
                className="btn btn-success btn-sm me-2 mb-1 d-flex align-items-center"
                to={`/projects/${params.id}/newlinks`}
              >
                <Icon
                  icon="mdi:plus-circle-outline"
                  fontSize={20}
                  className="me-2"
                />
                <span>Add new links</span>
              </Link>
              <ExportDropdown id={params.id} />
              <Link
                className="btn btn-primary btn-sm mb-1 d-flex align-items-center"
                to={`/projects/${params.id}/edit`}
              >
                <Icon icon="bx:edit" fontSize={20} className="me-2" />
                <span>Edit Project</span>
              </Link>
            </div>
          </div>
          <p className="mb-0">{project?.description}</p>
        </CardHeader>
        <CardBody>
          <div className="d-flex flex-wrap">
            <div
              className={`category-tab-item mb-1 ${category === 0 && "active"}`}
              onClick={() => handleChangeCategory(0)}
            >
              <Icon
                icon="solar:checklist-broken"
                fontSize={30}
                className="text-primary"
              />
              <div className="px-2">
                <p className="mb-1">All links</p>
                <h6 className="mb-0">
                  {project
                    ? parseInt(project?.total_urls) +
                      parseInt(project?.refunded_urls) +
                      parseInt(project?.nonindexable_refunded_urls)
                    : 0}
                </h6>
              </div>
            </div>
            <div
              className={`category-tab-item mb-1 ${category === 1 && "active"}`}
              onClick={() => handleChangeCategory(1)}
            >
              <Icon
                icon="bi:sort-down-alt"
                fontSize={35}
                className="text-warning"
              />
              <div className="px-2">
                <p className="mb-1"> Indexation in progress</p>
                <h6 className="mb-0">
                  {project
                    ? parseInt(project?.total_urls) -
                      parseInt(project?.indexed_urls) -
                      parseInt(project?.refunded_urls)
                    : 0}{" "}
                  (
                  {project
                    ? (
                        ((parseInt(project?.total_urls) -
                          parseInt(project?.indexed_urls) -
                          parseInt(project?.refunded_urls)) /
                          parseInt(project?.total_urls)) *
                        100
                      ).toFixed(2)
                    : "0.00"}
                  %)
                </h6>
              </div>
            </div>
            <div
              className={`category-tab-item mb-1 ${category === 2 && "active"}`}
              onClick={() => handleChangeCategory(2)}
            >
              <Icon
                icon="lets-icons:check-ring-round"
                fontSize={35}
                className="text-success"
              />
              <div className="px-2">
                <p className="mb-1">Indexed</p>
                <h6 className="mb-0">
                  {project?.indexed_urls} ({project?.indexed_percent}%)
                </h6>
              </div>
            </div>
            <div
              className={`category-tab-item mb-1 ${category === 3 && "active"}`}
              onClick={() => handleChangeCategory(3)}
            >
              <Icon
                icon="akar-icons:cross"
                fontSize={32}
                className="text-danger"
              />
              <div className="px-2">
                <p className="mb-1">Non indexed (Refunded)</p>
                <h6 className="mb-0">
                  {project?.refunded_urls} ({project?.refunded_percent}%)
                </h6>
              </div>
            </div>
            <div
              className={`category-tab-item mb-1 ${category === 4 && "active"}`}
              onClick={() => handleChangeCategory(4)}
            >
              <Icon
                icon="akar-icons:cross"
                fontSize={32}
                className="text-danger"
              />
              <div className="px-2">
                <p className="mb-1">Non indexables (Refunded)</p>
                <h6 className="mb-0">
                  {project?.nonindexable_refunded_urls} (
                  {renderNonIndexableRefundedPercent()}%)
                </h6>
              </div>
            </div>
            <div className="category-tab-item">{renderStatus()}</div>
          </div>
        </CardBody>
      </Card>
      <Card>
        <div className="dataTables_wrapper dt-bootstrap4 no-footer">
          <div className="p-3 border-bottom">
            <Row className="justify-between g-2">
              <Col className="col-7 text-start" lg="3" sm="4">
                <div className="dataTables_filter">
                  <InputGroup>
                    <InputGroupText>
                      <Icon icon="ri:search-line" />
                    </InputGroupText>
                    <Input
                      type="search"
                      placeholder="Search..."
                      value={search}
                      onChange={(ev) => setSearch(ev.target.value)}
                    />
                  </InputGroup>
                </div>
              </Col>
              <Col className="col-5 text-end" lg="9" sm="8">
                <div className="datatable-filter">
                  <div className="dataTables_length">
                    <label>
                      <div className="form-control-select">
                        <Input
                          type="select"
                          value={rowsPerPage}
                          onChange={(ev) => setRowsPerPage(ev.target.value)}
                        >
                          {rowsPerPageOptions.map((rowsPerPageOption, idx) => (
                            <option key={idx} value={rowsPerPageOption}>
                              {rowsPerPageOption}
                            </option>
                          ))}
                        </Input>
                      </div>
                    </label>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
          <DataTable
            className="border-bottom"
            columns={columns}
            data={links}
            pagination
            paginationServer
            paginationTotalRows={totalRows}
            paginationPerPage={rowsPerPage}
            paginationRowsPerPageOptions={rowsPerPageOptions}
            paginationComponent={({ rowCount }) => (
              <DataTablePagination
                currentPage={currentPage}
                rowsPerPage={rowsPerPage}
                rowCount={rowCount}
                onChangePage={setCurrentPage}
              />
            )}
            progressPending={loading}
            progressComponent={
              <div
                className="d-flex justify-content-center align-items-center"
                style={{ minHeight: 100 }}
              >
                <Spinner size="sm" color="dark" className="me-1" />{" "}
                <span>Please wait for a moment...</span>
              </div>
            }
            sortIcon={
              <div>
                <span>&darr;</span>
                <span>&uarr;</span>
              </div>
            }
            customStyles={{
              headCells: {
                style: {
                  paddingLeft: "12px !important",
                  paddingRight: "5px !important",
                },
              },
              cells: {
                style: {
                  paddingLeft: "12px !important",
                  paddingRight: "5px !important",
                },
              },
            }}
          />
        </div>
      </Card>
    </Fragment>
  );
};

export default ProjectDetail;
