import { Fragment, useState, useEffect } from "react";
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Row,
  Col,
  FormGroup,
  InputGroup,
  InputGroupText,
  Input,
  Label,
  Button,
  Spinner,
  Progress,
  Alert,
} from "reactstrap";
import { Link } from "react-router-dom";
import Http from "../services/Http";
import useAuth from "../hooks/useAuth";
import { Icon } from "@iconify/react";
import PhoneInput from "react-phone-input-2";
import moment from "moment";
import DataTable from "react-data-table-component";
import DataTablePagination from "../components/DataTablePagination";
import { toast } from "react-toastify";
import "react-phone-input-2/lib/style.css";

const Profile = () => {
  const { user, setUser, initAuth } = useAuth();
  const [profileLoading, setProfileLoading] = useState(false);
  const [passwordLoading, setPasswordLoading] = useState(false);
  const [tableLoading, setTableLoading] = useState(false);
  const [cancelLoading, setCancelLoading] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [prevPwd, setPrevPwd] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPwd, setConfirmPwd] = useState("");
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const rowsPerPageOptions = [25, 50, 100, 200];
  const [totalRows, setTotalRows] = useState(1000);
  const [histories, setHistories] = useState([]);
  const [isGetData, setIsGetData] = useState(false);

  const columns = [
    {
      name: "Membership",
      selector: (row) =>
        row.membership ? row.membership.name : "Deleted by administrator",
    },
    {
      name: "Price",
      selector: (row) => `$${row.amount.toFixed(2)}`,
    },
    {
      name: "Subscription ID",
      selector: (row) => row.subscription,
    },
    {
      name: "Date",
      selector: (row) => moment(row.createdAt).format("YYYY/MM/DD HH:mm:ss"),
    },
  ];

  useEffect(() => {
    setProfileLoading(false);
    setPasswordLoading(false);
    setFirstName(user?.firstName);
    setLastName(user?.lastName);
    setEmail(user?.email);
    setPhone(user?.phone);
    setAddress(user?.address);
    setPrevPwd("");
    setPassword("");
    setConfirmPwd("");
  }, []);

  useEffect(() => {
    fetchSubscriptionHistories();
  }, [search, rowsPerPage, currentPage, isGetData]);

  const fetchSubscriptionHistories = async () => {
    setTableLoading(true);
    const { data } = await Http.get(
      `/profile/${user?._id}/subscription-histories`,
      {
        params: {
          search,
          page: currentPage,
          rowsPerPage,
        },
      }
    );
    setHistories(data);
    setTotalRows(data.recordsFiltered);
    setTableLoading(false);
  };

  const handleUpdateProfile = async () => {
    if (firstName.trim() === "") {
      toast.error("Please enter a first name.");
    } else if (lastName.trim() === "") {
      toast.error("Please enter a last name.");
    } else if (email.trim() === "") {
      toast.error("Please enter an email address.");
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email)) {
      toast.error("Please enter a valid email.");
    } else {
      try {
        setProfileLoading(true);
        const { data } = await Http.put(`/profile/${user._id}/update`, {
          firstName,
          lastName,
          email,
          phone,
        });

        if (data.status) {
          setUser(data.user);
          toast.success(data.msg);
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setProfileLoading(false), 500);
      }
    }
  };

  const handleUpdatePassword = async () => {
    if (prevPwd.trim() === "") {
      toast.error("Please enter a current password.");
    } else if (password.trim() === "") {
      toast.error("Please enter a new password.");
    } else if (password.trim() !== confirmPwd.trim()) {
      toast.error("Passwords don't match.");
    } else {
      try {
        setPasswordLoading(true);
        const { data } = await Http.put(`/profile/${user._id}/password`, {
          prevPassword: prevPwd,
          password,
        });

        if (data.status) {
          toast.success(data.msg);
          setPrevPwd("");
          setPassword("");
          setConfirmPwd("");
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setPasswordLoading(false), 500);
      }
    }
  };

  const getSpentDates = () => {
    return moment().diff(moment(user.membershipStartAt), "days");
  };

  const getTotalDates = () => {
    return moment(user.membershipEndAt).diff(
      moment(user.membershipStartAt),
      "days"
    );
  };

  const handleCancelSubscription = async () => {
    try {
      setCancelLoading(true);
      const { data } = await Http.delete(
        `/memberships/${user?._id}/subscription`
      );
      if (data.status) {
        toast.success(data.msg);
        initAuth();
      } else {
        toast.error(data.msg);
      }
    } catch (err) {
      toast.error(err.message);
    } finally {
      setCancelLoading(false);
    }
  };

  return (
    <Fragment>
      <Row>
        <Col lg={6} xs={12}>
          <Card className="mb-3">
            <CardHeader className="bg-white p-3">
              <h4 className="mb-0">User Information</h4>
            </CardHeader>
            <CardBody>
              <Row>
                <Col md={6}>
                  <FormGroup>
                    <Label>First Name</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="icon-park-outline:edit-name" />
                      </InputGroupText>
                      <Input
                        type="text"
                        value={firstName}
                        onChange={(ev) => setFirstName(ev.target.value)}
                      />
                    </InputGroup>
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label>Last Name</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="icon-park-outline:edit-name" />
                      </InputGroupText>
                      <Input
                        type="text"
                        value={lastName}
                        onChange={(ev) => setLastName(ev.target.value)}
                      />
                    </InputGroup>
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label>Email</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="ic:outline-email" />
                      </InputGroupText>
                      <Input
                        type="email"
                        value={email}
                        onChange={(ev) => setEmail(ev.target.value)}
                      />
                    </InputGroup>
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label>Phone</Label>
                    <PhoneInput
                      country="us"
                      specialLabel=""
                      value={phone}
                      onChange={(phone) => setPhone(phone)}
                      inputStyle={{ width: "100%" }}
                    />
                  </FormGroup>
                </Col>
                <Col md={12}>
                  <div>
                    <Label>Address</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="ph:address-book" />
                      </InputGroupText>
                      <Input
                        type="text"
                        value={address}
                        onChange={(ev) => setAddress(ev.target.value)}
                        autoComplete="new-password"
                      />
                    </InputGroup>
                  </div>
                </Col>
              </Row>
            </CardBody>
            <CardFooter className="d-flex justify-content-end bg-white py-3">
              <Button
                disabled={profileLoading}
                color="primary"
                className="d-flex align-items-center gap-2"
                onClick={handleUpdateProfile}
              >
                {profileLoading ? (
                  <Spinner size="sm" />
                ) : (
                  <Icon icon="material-symbols:save-outline" fontSize={20} />
                )}
                Update Profile
              </Button>
            </CardFooter>
          </Card>
        </Col>
        <Col lg={6} xs={12}>
          <Card className="mb-3">
            <CardHeader className="bg-white p-3">
              <h4 className="mb-0">Change password</h4>
            </CardHeader>
            <CardBody>
              <FormGroup className="required">
                <Label>Current Password</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="teenyicons:password-outline" />
                  </InputGroupText>
                  <Input
                    type="password"
                    value={prevPwd}
                    onChange={(ev) => setPrevPwd(ev.target.value)}
                    autoComplete="new-password"
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup className="required">
                <Label>New Password</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="solar:key-broken" />
                  </InputGroupText>
                  <Input
                    type="password"
                    value={password}
                    onChange={(ev) => setPassword(ev.target.value)}
                    autoComplete="new-password"
                  />
                </InputGroup>
              </FormGroup>
              <div className="required">
                <Label>Password Confirmation</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="mdi:lock-check-outline" />
                  </InputGroupText>
                  <Input
                    type="password"
                    value={confirmPwd}
                    onChange={(ev) => setConfirmPwd(ev.target.value)}
                  />
                </InputGroup>
              </div>
            </CardBody>
            <CardFooter className="d-flex justify-content-end bg-white py-3">
              <Button
                disabled={passwordLoading}
                color="primary"
                className="d-flex align-items-center gap-2"
                onClick={handleUpdatePassword}
              >
                {passwordLoading ? (
                  <Spinner size="sm" />
                ) : (
                  <Icon icon="material-symbols:save-outline" fontSize={20} />
                )}
                Update Password
              </Button>
            </CardFooter>
          </Card>
        </Col>
        <Col xs={12}>
          <Card>
            <CardHeader className="d-flex align-items-center bg-white p-3">
              <h4 className="mb-0">Subscription Details</h4>
            </CardHeader>
            <CardBody>
              {user.membership ? (
                <Row>
                  <Col md={6} xs={12}>
                    <h5>Your current plan is {user.membership.name}</h5>
                    <div className="ps-3">
                      {user.membership.description &&
                        user.membership.description
                          .split(/\r*\n/)
                          .map((item, idx) => (
                            <p className="mb-0 d-flex gap-2" key={idx}>
                              <Icon
                                icon="ph:seal-check-bold"
                                fontSize={20}
                                className="mt-1"
                              />
                              <span style={{ flex: 1 }}>{item}</span>
                            </p>
                          ))}
                    </div>
                  </Col>
                  <Col md={6} xs={12}>
                    <h5>
                      Active until {moment(user.membershipEndAt).format("LL")}
                    </h5>
                    <p>
                      We will send you a notification upon Subscription
                      expiration.
                    </p>
                    <div className="d-flex justify-content-between">
                      <p className="mb-0 font-weight-bold">Days</p>
                      <p className="mb-0 font-weight-bold">
                        {getSpentDates()} of {getTotalDates()} Days
                      </p>
                    </div>
                    <Progress
                      striped
                      color="success"
                      className="progress-lg"
                      value={Math.ceil(
                        (getSpentDates() * 100) / getTotalDates()
                      )}
                    >
                      {Math.ceil((getSpentDates() * 100) / getTotalDates())}%
                    </Progress>
                    <p>{getTotalDates() - getSpentDates()} days remaining</p>
                    <div className="mb-4">
                      <Link
                        color="primary"
                        className="btn btn-primary me-2"
                        to="/memberships"
                      >
                        Upgrade Memberhip
                      </Link>
                      <Button
                        color="danger"
                        disabled={cancelLoading}
                        onClick={handleCancelSubscription}
                      >
                        {cancelLoading && (
                          <Spinner size="sm" color="light" className="me-1" />
                        )}{" "}
                        Cancel Subscription
                      </Button>
                    </div>
                  </Col>
                </Row>
              ) : (
                <div>
                  <Alert
                    className="alert-icon d-flex align-items-center gap-2"
                    color="warning"
                  >
                    <Icon icon="mdi:alert-circle-outline" fontSize={20} />
                    <span>
                      <strong className="me-1">Announcemence!</strong> You need
                      to subscribe a membership to analyze SEO.
                    </span>
                  </Alert>
                  <div className="text-end">
                    <Link
                      color="primary"
                      className="btn btn-primary"
                      to="/memberships"
                    >
                      Subscribe Membership
                    </Link>
                  </div>
                </div>
              )}
            </CardBody>
            <div className="dataTables_wrapper dt-bootstrap4 no-footer border-top">
              <Row className="border-bottom justify-between p-3 g-2">
                <Col className="col-7 text-start" lg="3" sm="4">
                  <div className="dataTables_filter">
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="ri:search-line" />
                      </InputGroupText>
                      <Input
                        type="search"
                        placeholder="Search..."
                        value={search}
                        onChange={(ev) => setSearch(ev.target.value)}
                      />
                    </InputGroup>
                  </div>
                </Col>
                <Col className="col-5 text-end" lg="9" sm="8">
                  <div className="datatable-filter">
                    <div className="dataTables_length">
                      <Label className="mb-0">
                        <Input
                          type="select"
                          value={rowsPerPage}
                          onChange={(ev) => setRowsPerPage(ev.target.value)}
                        >
                          {rowsPerPageOptions.map((rowsPerPageOption, idx) => (
                            <option key={idx} value={rowsPerPageOption}>
                              {rowsPerPageOption}
                            </option>
                          ))}
                        </Input>
                      </Label>
                    </div>
                  </div>
                </Col>
              </Row>
              <DataTable
                className="border-bottom"
                columns={columns}
                data={histories}
                pagination
                paginationServer
                paginationTotalRows={totalRows}
                paginationPerPage={rowsPerPage}
                paginationRowsPerPageOptions={rowsPerPageOptions}
                paginationComponent={({ rowCount, onChangeRowsPerPage }) => (
                  <DataTablePagination
                    currentPage={currentPage}
                    rowsPerPage={rowsPerPage}
                    rowCount={rowCount}
                    onChangePage={setCurrentPage}
                  />
                )}
                progressPending={tableLoading}
                progressComponent={
                  <div
                    className="d-flex justify-content-center align-items-center"
                    style={{ minHeight: 100 }}
                  >
                    <Spinner size="sm" color="dark" className="me-1" />{" "}
                    <span>Please wait for a moment...</span>
                  </div>
                }
                sortIcon={
                  <div>
                    <span>&darr;</span>
                    <span>&uarr;</span>
                  </div>
                }
                customStyles={{
                  headCells: {
                    style: {
                      paddingLeft: "12px !important",
                      paddingRight: "5px !important",
                    },
                  },
                  cells: {
                    style: {
                      paddingLeft: "12px !important",
                      paddingRight: "5px !important",
                    },
                  },
                }}
              />
            </div>
          </Card>
        </Col>
      </Row>
    </Fragment>
  );
};

export default Profile;
