import { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  Form,
  FormGroup,
  InputGroup,
  InputGroupText,
  Label,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import { Link, useNavigate } from "react-router-dom";
import { Icon } from "@iconify/react";
import { toast } from "react-toastify";
import useAuth from "../hooks/useAuth";
import Http from "../services/Http";
import "./Login.css";

const Login = () => {
  const navigate = useNavigate();
  const { setUser, setToken } = useAuth();
  const [loading, setLoading] = useState(false);
  const [email, setEmail] = useState("");
  const [passState, setPassState] = useState(false);
  const [password, setPassword] = useState("");

  const handleLogin = async (ev) => {
    ev.preventDefault();
    if (email.trim() === "") {
      toast.error("Please enter a email.");
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email)) {
      toast.error("Please enter a valid email.");
    } else if (password.trim() === "") {
      toast.error("Please enter a password.");
    } else {
      try {
        setLoading(true);
        const { data } = await Http.post("/auth/login", {
          email,
          password,
        });

        if (data.status) {
          localStorage.setItem("token", data.token);
          localStorage.setItem("user", JSON.stringify(data.user));
          setToken(data.token);
          setUser(data.user);
          toast.success(data.msg);
          navigate("/");
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  return (
    <div className="login-container">
      <Card className="login-form">
        <CardBody className="p-4">
          <Form className="p-1" onSubmit={handleLogin}>
            <div className="text-center mb-3">
              <img src="/logo.png" height={60} alt="logo" />
            </div>
            <h4>Sign In</h4>
            <p>Access to the panel using your email and password.</p>
            <FormGroup>
              <Label>Email:</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ic:outline-email" />
                </InputGroupText>
                <Input
                  type="email"
                  value={email}
                  onChange={(ev) => setEmail(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
            <FormGroup>
              <div className="d-flex align-items-center justify-content-between">
                <Label>Password:</Label>
                <Link to="/forgot-password" className="link">
                  Forgot Password?
                </Link>
              </div>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ph:lock-bold" />
                </InputGroupText>
                <Input
                  type={passState ? "text" : "password"}
                  value={password}
                  onChange={(ev) => setPassword(ev.target.value)}
                  autoComplete="new-password"
                />
                <Button
                  type="button"
                  color={passState ? "danger" : "primary"}
                  outline
                  onClick={() => setPassState((passState) => !passState)}
                >
                  <Icon icon={passState ? "mdi:eye-off" : "mdi:eye"} />
                </Button>
              </InputGroup>
            </FormGroup>
            <Button
              color="primary"
              className="mt-4 mb-3 w-100 d-flex align-items-center justify-content-center gap-2"
            >
              {loading ? (
                <Spinner size="sm" />
              ) : (
                <Icon icon="mdi:sign-in" fontSize={20} />
              )}
              Login
            </Button>
            <p className="text-center mb-0">
              New on our platform?{" "}
              <Link to="/register" className="link">
                Create an account
              </Link>
            </p>
          </Form>
        </CardBody>
      </Card>
    </div>
  );
};

export default Login;
