import moment from "moment";

export const findUpper = (string) => {
  let extractedString = [];

  for (var i = 0; i < string.length; i++) {
    if (
      string.charAt(i) === string.charAt(i).toUpperCase() &&
      string.charAt(i) !== " "
    ) {
      extractedString.push(string.charAt(i));
    }
  }
  if (extractedString.length > 1) {
    return extractedString[0] + extractedString[1];
  } else {
    return extractedString[0];
  }
};

export const checkMembership = (user) => {
  if (
    user.membership &&
    user.membershipStartAt &&
    user.membershipEndAt &&
    moment().add(5, "minutes").isSameOrAfter(user.membershipStartAt) &&
    moment().isSameOrBefore(user.membershipEndAt)
  ) {
    return true;
  } else {
    return false;
  }
};
