import { useState } from "react";
import {
  Card,
  CardBody,
  Form,
  FormGroup,
  InputGroup,
  InputGroupText,
  Label,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import { Link } from "react-router-dom";
import { Icon } from "@iconify/react";
import Http from "../services/Http";
import { toast } from "react-toastify";
import "./ForgotPwd.css";

const ForgotPwd = () => {
  const [loading, setLoading] = useState(false);
  const [email, setEmail] = useState("");
  const [isSent, setIsSent] = useState(false);

  const handleForgotPwd = async () => {
    if (email.trim() === "") {
      toast.error("Please enter an email address.");
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email)) {
      toast.error("Please enter a valid email.");
    } else {
      try {
        setLoading(true);
        const { data } = await Http.post("/auth/forgot-pwd", {
          email,
        });

        if (data.status) {
          toast.success(data.msg);
          setIsSent(true);
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  return (
    <div className="forgot-password-container">
      <Card className="forgot-password-form">
        <CardBody className="p-4">
          {!isSent ? (
            <Form className="p-1">
              <div className="text-center mb-3">
                <img src="/logo.png" height={60} alt="logo" />
              </div>
              <h4>Reset password</h4>
              <p>
                If you forgot your password, well, then we will email you
                instructions to reset your password.
              </p>
              <FormGroup>
                <Label>Email</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="ic:outline-email" />
                  </InputGroupText>
                  <Input
                    type="email"
                    value={email}
                    onChange={(ev) => setEmail(ev.target.value)}
                  />
                </InputGroup>
              </FormGroup>
              <Button
                color="primary"
                disabled={loading}
                className="w-100 mt-4 mb-2 d-flex align-items-center gap-2 justify-content-center"
                onClick={handleForgotPwd}
              >
                {loading ? (
                  <Spinner size="sm" />
                ) : (
                  <Icon icon="quill:send" fontSize={20} />
                )}
                Send Reset Link
              </Button>
              <div className="text-center">
                <Link to="/login" className="link">
                  Return to login
                </Link>
              </div>
            </Form>
          ) : (
            <div className="text-center py-2">
              <h5 className="mb-4">
                We sent a link to reset your password into {email}.
              </h5>
              <p>Please check your email box.</p>
              <Link to="/login" className="btn btn-primary px-4">
                Return to login
              </Link>
            </div>
          )}
        </CardBody>
      </Card>
    </div>
  );
};

export default ForgotPwd;
