import { Badge } from "reactstrap";

const IndexingStatusBadge = ({ row }) => {
  if (parseInt(row.indexed) === 1) {
    return <Badge className="badge-dim px-2 py-1" color="success">Indexed</Badge>
  } else if (parseInt(row.refunded) === 1) {
    if (parseInt(row.indexable) !== 2) {
        return <Badge className="badge-dim px-2 py-1" color="danger">Non indexed (refunded)</Badge>
    } else {
        return <Badge className="badge-dim px-2 py-1" color="danger">Non indexable (refunded)</Badge>
    }
  } else {
    return <Badge className="badge-dim px-2 py-1" color="warning">Requests in progress</Badge>
  }
};

export default IndexingStatusBadge;
