import { Fragment, useState, useEffect } from "react";
import { Row, Col, Card, CardBody, Button } from "reactstrap";
import { Icon } from "@iconify/react";
import { PayPalScriptProvider, PayPalButtons } from "@paypal/react-paypal-js";
import useAuth from "../hooks/useAuth";
import Http from "../services/Http";
import { toast } from "react-toastify";
import "./MembershipList.css";

const MembershipList = () => {
  const { user, initAuth } = useAuth();
  const [options, setOptions] = useState(null);
  const [memberships, setMemberships] = useState([]);

  useEffect(() => {
    fetchMemberships();
  }, []);

  const fetchMemberships = async () => {
    const { data } = await Http.get("/memberships");
    setMemberships(data.memberships);
    setOptions({
      "client-id": data.setting.paypalClientID,
      currency: "USD",
      vault: true,
      intent: "subscription",
    });
  };

  const isSubscribed = (id) => {
    if (user.membership && user.membership._id === id) {
      return true;
    }

    return false;
  };

  const createSubscription = async (id) => {
    try {
      const { data } = await Http.post(`/memberships/${id}/subscription`, {
        user: user._id,
      });

      if (data.status) {
        return data.id;
      } else {
        toast.error(data.msg);
        return;
      }
    } catch (err) {
      toast.error(err.message);
      return;
    }
  };

  const approveSubscription = async ({ subscriptionID: id }, actions) => {
    const { data } = await Http.put(`/memberships/${id}/subscription`, {
      user: user._id,
    });

    if (data.status) {
      toast.success(data.msg);
      initAuth();
    } else {
      toast.error(data.msg);
      return;
    }
  };

  const errorSubscription = (err) => {
    toast.error(err.message);

    return Fragment;
  };

  return (
    <div className="d-flex flex-column align-items-center justify-content-center">
      <h1 className="text-center mb-4">Choose A Package</h1>
      <div className="d-flex flex-wrap align-items-center justify-content-center gap-3">
        {memberships.map((membership, idx) => (
          <Card key={idx} className="membership-card mb-3">
            <CardBody className="p-4">
              <h4 className="text-center mb-3">{membership.name}</h4>
              <h1 className="text-center mb-3 fw-bold" style={{ fontSize: 50 }}>
                $ {membership.price.toFixed(2)}
              </h1>
              <div className="mb-3">
                <p className="mb-0 d-flex gap-2">
                  <Icon
                    icon="ph:seal-check-bold"
                    fontSize={20}
                    className="mt-1"
                  />
                  <span style={{ flex: 1 }}>{membership.credit} credits</span>
                </p>
                {membership.description.split(/\r*\n/).map((item, idx) => (
                  <p className="mb-0 d-flex gap-2" key={idx}>
                    <Icon
                      icon="ph:seal-check-bold"
                      fontSize={20}
                      className="mt-1"
                    />
                    <span style={{ flex: 1 }}>{item}</span>
                  </p>
                ))}
              </div>
              {isSubscribed(membership._id) ? (
                <Button
                  color="success"
                  disabled
                  className="w-100 justify-content-center mb-1"
                  style={{ fontSize: 18 }}
                >
                  Subscribed
                </Button>
              ) : (
                options && (
                  <PayPalScriptProvider options={options}>
                    <PayPalButtons
                      style={{
                        shape: "rect",
                        color: "blue",
                        height: 40,
                        layout: "horizontal",
                        label: "subscribe",
                        tagline: false,
                      }}
                      showSpinner
                      createSubscription={() =>
                        createSubscription(membership.plan)
                      }
                      onApprove={(data, actions) =>
                        approveSubscription(data, actions)
                      }
                      onError={(err) => errorSubscription(err)}
                    />
                  </PayPalScriptProvider>
                )
              )}
            </CardBody>
          </Card>
        ))}
      </div>
    </div>
  );
};

export default MembershipList;
