import { Button } from "reactstrap";
import { Link } from "react-router-dom";

const NotFoundPage = () => {
  return (
    <>
      <div className="nk-block-middle wide-md mx-auto">
        <div className="nk-error-ld text-center">
          <img
            className="nk-error-gfx"
            src="/images/error-404.svg"
            alt="error"
          />
          <div className="wide-xs mx-auto">
            <h3 className="nk-error-title">Oops! Why youre here?</h3>
            <p className="nk-error-text">
              We are very sorry for inconvenience. It looks like youre try to
              access a page that either has been deleted or never existed.
            </p>
            <Link to={`${process.env.PUBLIC_URL}/`}>
              <Button color="primary" className="mt-2">
                Back To Home
              </Button>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};
export default NotFoundPage;
