import { useState, useEffect } from "react";
import Http from "../services/Http";
import { toast } from "react-toastify";
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Row,
  Col,
  FormGroup,
  Label,
  Input,
  Button,
  Alert,
  Spinner,
} from "reactstrap";
import { Icon } from "@iconify/react";
import useAuth from '../hooks/useAuth'
import { checkMembership } from "../services/Utils";

const NewProject = () => {
  const { user } = useAuth()
  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [links, setLinks] = useState("");
  const [linkLimit, setLinkLimit] = useState(5);

  useEffect(() => {
    fetchLimitLinks();
  }, []);

  const fetchLimitLinks = async () => {
    const { data } = await Http.get("/projects/limit-links");
    setLinkLimit(data);
  };

  const handleChangeDescription = (ev) => {
    if (ev.target.value.trim().length > 250) {
      toast.error("Project description is limited to 250 characters.");
    } else {
      setDescription(ev.target.value);
    }
  };

  const handleChangeLinkList = (ev) => {
    let length = ev.target.value.split(/\r*\n/).length;
    if (length > linkLimit) {
      toast.error(`You can add maximum ${linkLimit} links at once.`);
    } else {
      setLinks(ev.target.value);
    }
  };

  const handleCreateNewProject = async () => {
    if (checkMembership(user)) {
      if (name.trim() === "") {
        toast.error("Please enter a project name.");
      } else if (description.trim().length > 250) {
        toast.error("Project description is limited to 250 characters.");
      } else if (links.split(/\r*\n/).length > linkLimit) {
        toast.error(`You can add maximum ${linkLimit} links at once.`);
      } else {
        try {
          setLoading(true);
          const { data } = await Http.post("/projects", {
            user: user._id,
            name,
            description,
            urlslist: links,
          });
  
          if (data.status) {
            setName("");
            setDescription("");
            setLinks("");
            for (let msg of data.msgs) {
              toast.success(msg);
            }
          } else {
            for (let msg of data.msgs) {
              toast.error(msg);
            }
          }
        } catch (err) {
          toast.error(err.message);
        } finally {
          setTimeout(() => setLoading(false), 500);
        }
      }
    } else {
      toast.error('Subscribe to our membership to take advantage of the features provided please.')
    }
  };

  return (
    <Card>
      <CardHeader className="p-3 bg-white d-flex align-items-center justify-content-between">
        <h4 className="mb-0">New Project</h4>
      </CardHeader>
      <CardBody>
        <FormGroup className="required">
          <Row>
            <Col xl={3} md={4} sm={5} xs={12}>
              <Label>Project name</Label>
            </Col>
            <Col xl={9} md={8} sm={7} xs={12}>
              <Input
                type="text"
                value={name}
                onChange={(ev) => setName(ev.target.value)}
              />
            </Col>
          </Row>
        </FormGroup>
        <FormGroup>
          <Row>
            <Col xl={3} lg={4} md={4} sm={5} xs={12}>
              <Label>Description</Label>
            </Col>
            <Col xl={9} lg={8} md={8} sm={7} xs={12}>
              <Input
                type="text"
                value={description}
                onChange={handleChangeDescription}
              />
            </Col>
          </Row>
        </FormGroup>
        <FormGroup className="required">
          <Row>
            <Col xl={3} lg={4} md={4} sm={5} xs={12}>
              <Label>Links list (one by line)</Label>
            </Col>
            <Col xl={9} lg={8} md={8} sm={7} xs={12}>
              <Input
                type="textarea"
                value={links}
                rows={10}
                onChange={handleChangeLinkList}
              />
            </Col>
          </Row>
        </FormGroup>
        <FormGroup>
          <Row>
            <Col xl={3} lg={4} md={4} sm={5} xs={12}>
              <Label>Nb estimated credit(s) avec dduplication</Label>
            </Col>
            <Col xl={9} lg={8} md={8} sm={7} xs={12}>
              <p className="mb-1">
                {links.split(/\r*\n/).filter((link) => link !== "").length}
              </p>
            </Col>
          </Row>
        </FormGroup>
        <Alert className="alert-icon" color="warning">
          <Icon icon="carbon:warning" fontSize={24} className="me-2" />
          <strong className="me-1">Warning!</strong> On project saving, credits
          will be deducted according to the number of urls. Urls not indexed
          after 10 days will be re-credited.
        </Alert>
      </CardBody>
      <CardFooter className="bg-white d-flex justify-content-end py-3">
        <Button color="primary" disabled={loading} className="d-flex align-items-center" onClick={handleCreateNewProject}>
          {loading ? (
            <Spinner size="sm" color="light" className="me-2" />
          ) : (
            <Icon
              icon="material-symbols:save-outline"
              fontSize={20}
              className="me-2"
            />
          )}
          <span>Create Project</span>
        </Button>
      </CardFooter>
    </Card>
  );
};

export default NewProject;
