import { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  Form,
  Row,
  Col,
  FormGroup,
  InputGroup,
  InputGroupText,
  Label,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import { Link, useNavigate } from "react-router-dom";
import { Icon } from "@iconify/react";
import { toast } from "react-toastify";
import Http from "../services/Http";
import "./Register.css";

const Register = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirmation, setPasswordConfirmation] = useState("");

  const handleRegister = async (ev) => {
    ev.preventDefault();
    if (firstName.trim() === "") {
      toast.error("Please enter a first name.");
    } else if (lastName.trim() === "") {
      toast.error("Please enter a last name.");
    } else if (email.trim() === "") {
      toast.error("Please enter an email address.");
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email)) {
      toast.error("Please enter a valid email.");
    } else if (password.trim() === "") {
      toast.error("Please eneter a password.");
    } else if (password.trim() !== passwordConfirmation.trim()) {
      toast.error("Passwords don't match.");
    } else {
      try {
        setLoading(true);
        const { data } = await Http.post("/auth/register", {
          firstName,
          lastName,
          email,
          password,
        });

        if (data.status) {
          toast.success(data.msg);
          navigate("/login");
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  return (
    <div className="register-container">
      <Card className="register-form">
        <CardBody className="p-4">
          <Form className="p-1" onSubmit={handleRegister}>
            <div className="text-center mb-3">
              <img src="/logo.png" height={60} alt="logo" />
            </div>
            <h4>Register</h4>
            <p>Please fill information below to register.</p>
            <Row>
              <Col md={6} xs={12}>
                <FormGroup className="required">
                  <Label>First Name:</Label>
                  <InputGroup>
                    <InputGroupText>
                      <Icon icon="icon-park-outline:edit-name" />
                    </InputGroupText>
                    <Input
                      type="text"
                      value={firstName}
                      onChange={(ev) => setFirstName(ev.target.value)}
                    />
                  </InputGroup>
                </FormGroup>
              </Col>
              <Col md={6} xs={12}>
                <FormGroup className="required">
                  <Label>Last Name:</Label>
                  <InputGroup>
                    <InputGroupText>
                      <Icon icon="icon-park-outline:edit-name" />
                    </InputGroupText>
                    <Input
                      type="text"
                      value={lastName}
                      onChange={(ev) => setLastName(ev.target.value)}
                    />
                  </InputGroup>
                </FormGroup>
              </Col>
            </Row>
            <FormGroup className="required">
              <Label>Email:</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ic:outline-email" />
                </InputGroupText>
                <Input
                  type="email"
                  value={email}
                  onChange={(ev) => setEmail(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
            <FormGroup className="required">
              <Label>Password:</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ph:lock-bold" />
                </InputGroupText>
                <Input
                  type="password"
                  value={password}
                  onChange={(ev) => setPassword(ev.target.value)}
                  autoComplete="new-password"
                />
              </InputGroup>
            </FormGroup>
            <FormGroup className="required">
              <Label>Password Confirmation:</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="line-md:confirm-circle" />
                </InputGroupText>
                <Input
                  type="password"
                  value={passwordConfirmation}
                  onChange={(ev) => setPasswordConfirmation(ev.target.value)}
                  autoComplete="new-password"
                />
              </InputGroup>
            </FormGroup>
            <Button
              color="primary"
              disabled={loading}
              className="mt-4 mb-3 w-100 d-flex align-items-center justify-content-center gap-2"
            >
              {loading ? (
                <Spinner size="sm" />
              ) : (
                <Icon icon="mingcute:registered-line" fontSize={20} />
              )}{" "}
              Register Now
            </Button>
            <p className="text-center mb-0">
              Already have an account?{" "}
              <Link to="/login" className="link">
                Sign in instead
              </Link>
            </p>
          </Form>
        </CardBody>
      </Card>
    </div>
  );
};

export default Register;
