import React, { useLayoutEffect, Fragment } from "react";
import { Routes, Route, Navigate, useLocation } from "react-router-dom";
import useAuth from "../hooks/useAuth";

import ProtectedRoute from "./ProtectedRoute";

import Login from "../pages/Login";
import Register from "../pages/Register";
import ForgotPwd from "../pages/ForgotPwd";
import ResetPwd from "../pages/ResetPwd";
import Profile from "../pages/Profile";
import ProjectList from "../pages/ProjectList.jsx";
import NewProject from "../pages/NewProject";
import EditProject from "../pages/EditProject";
import ProjectDetail from "../pages/ProjectDetail";
import NewLinks from "../pages/NewLinks";
import Cache from "../pages/Cache";
import MembershipList from "../pages/MembershipList";
import NotFound from "../pages/NotFound";

import Layout from "../layouts/Index";
import LayoutBlank from "../layouts/IndexBlank";
import LoadingScreen from "../components/LoadingScreen";

const Router = () => {
  const { loading } = useAuth();
  const location = useLocation();

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <Fragment>
      <Routes>
        <Route
          path={`${process.env.PUBLIC_URL}`}
          element={
            <ProtectedRoute>
              <Layout />
            </ProtectedRoute>
          }
        >
          <Route index element={<Navigate to="/projects" replace />}></Route>
          <Route path="projects" element={<ProjectList />}></Route>
          <Route path="projects/new" element={<NewProject />}></Route>
          <Route path="projects/:id" element={<ProjectDetail />}></Route>
          <Route path="projects/:id/edit" element={<EditProject />}></Route>
          <Route path="projects/:id/newlinks" element={<NewLinks />}></Route>
          <Route path="memberships" element={<MembershipList />}></Route>
          <Route path="profile" element={<Profile />}></Route>
        </Route>
        <Route path={`${process.env.PUBLIC_URL}`} element={<LayoutBlank />}>
          <Route path="login" element={<Login />}></Route>
          <Route path="register" element={<Register />}></Route>
          <Route path="forgot-password" element={<ForgotPwd />}></Route>
          <Route path="reset-password" element={<ResetPwd />}></Route>
          <Route
            path="projects/:projectId/cache/:id"
            element={
              <ProtectedRoute>
                <Cache />
              </ProtectedRoute>
            }
          ></Route>
          <Route path="*" element={<NotFound />}></Route>
        </Route>
      </Routes>
      {loading && <LoadingScreen />}
    </Fragment>
  );
};

export default Router;
